import React from "react";
import { Link } from "react-router-dom";

import { getFormattedTime } from "../../services";

import "./slidePromo.scss";

const SlidePromo = ({ title, url, imageSrc, imageWebp, imageAlt, dateTime }) => {
	return (
		<div className="slide-promo">
			<Link className="slide-promo__image" to="/anypage" aria-label="   " tabIndex={-1}>
				<img className="swiper-lazy" data-src={imageSrc} data-srcset={imageWebp} alt={imageAlt} />
				<div className="swiper-lazy-preloader"></div>
			</Link>
			<div className="slide-promo__content">
				<div className="slide-promo__info">
					<time className="slide-promo__time" dateTime={dateTime}>
						{getFormattedTime(dateTime)}
					</time>
					<span className="slide-promo__label"></span>
				</div>
				<Link className="slide-promo__title" to="/anypage" aria-label="   ">
					{title}
				</Link>
			</div>
		</div>
	);
};

export default SlidePromo;
