import React from "react";

import "./customTablist.scss";

const CustomTablist = ({ className, tablist, selectedTab, changeSelectedTab }) => {
	const handleKeyDown = (event) => {
		if (event.code === "Space" || event.code === "Enter") {
			event.preventDefault();
			changeSelectedTab(event.target.textContent);
		}
	};
	return (
		tablist &&
		tablist.length && (
			<ul className={"tablist" + (className ? ` ${className}` : "")} role="tablist">
				{tablist.map((tabHeader) => (
					<li
						key={tabHeader}
						className={"tab" + (tabHeader === selectedTab ? " selected" : "")}
						tabIndex="0"
						role="tab"
						aria-selected={selectedTab === tabHeader}
						onClick={() => changeSelectedTab(tabHeader)}
						onKeyDown={handleKeyDown}
					>
						{tabHeader}
					</li>
				))}
			</ul>
		)
	);
};

export default CustomTablist;
