import React from "react";
import { Link } from "react-router-dom";

import { useMatchMedia } from "../../hooks";
import SectionArticles from "../sectionArticles/SectionArticles";
import SectionNews from "../sectionNews/SectionNews";
import NewsArticlesMobile from "../newsArticlesMobile/NewsArticlesMobile";

import "./mainSection.scss";

const MainSection = ({ sectionTitle }) => {
	const { isMobile } = useMatchMedia();
	return (
		<section className="main-section">
			<div className="main-section__header">
				<h3 className="main-section__title title">{sectionTitle}</h3>
				<Link
					className="main-section__more"
					to="/anypage"
					aria-label={`     ${sectionTitle}`}
				>
					  
				</Link>
			</div>
			<div className="main-section__content">
				{isMobile ? (
					<div className="main-section__mobile">
						<NewsArticlesMobile />
					</div>
				) : (
					<>
						<div className="main-section__item">
							<SectionArticles />
						</div>
						<div className="main-section__item">
							<SectionNews title=" " />
						</div>
					</>
				)}
			</div>
		</section>
	);
};

export default MainSection;
