import React from "react";
import classNames from "classnames";
import { Link } from "react-router-dom";

import { getFormattedTime } from "../../services";

import "./itemVideo.scss";

const ItemVideo = ({ className, title, dateTime, url, image, isLarge }) => {
	const itemClassnames = classNames({
		[className]: className,
		"item-video": true,
		"item-video--large": isLarge,
	});

	return (
		<li className={itemClassnames}>
			<Link className="item-video__link" to="/anypage">
				<picture className="item-video__preview">
					<source srcSet={image.webp} type="image/webp" />
					<img src={image.src} alt={image.alt} />
				</picture>
			</Link>
			<time dateTime={dateTime} className="item-video__time">
				{getFormattedTime(dateTime)}
			</time>
			<Link className="item-video__title" to="/anypage">
				{title}
			</Link>
		</li>
	);
};

export default ItemVideo;
