import React from "react";

import { useMockAPI } from "../../hooks";
import BlockArticle from "../blockArticle/BlockArticle";
import BlockArticleSkeleton from "../blockArticleSkeleton/BlockArticleSkeleton";

import "./sectionArticles.scss";

const SectionArticles = () => {
	const { loading, error, getItems } = useMockAPI();
	const [articles, setArticles] = React.useState(null);

	React.useEffect(() => {
		getArticles();
	}, []);

	const getArticles = async () => {
		const request = await getItems("politic-articles", 1, 3).then(setArticles);
	};
	return (
		<div className="section-articles">
			<div className="section-articles__body">
				{loading && !articles && (
					<ul className="section-articles__main-articles" aria-hidden>
						{new Array(3).fill(null).map((item, i) => (
							<BlockArticleSkeleton key={i} />
						))}
					</ul>
				)}
				{!error && articles && articles.length && (
					<ul className="section-articles__main-articles">
						{articles.map((item, index) => (
							<li key={item.dateTime} className="section-articles__article">
								<BlockArticle {...item} isLarge={index === 0} />
							</li>
						))}
					</ul>
				)}
			</div>
		</div>
	);
};

export default SectionArticles;
