import React from "react";

import BlockColumns from "../blockColumns/BlockColumns";
import MainSection from "../mainSection/MainSection";
import SectionPromo from "../sectionPromo/SectionPromo";

import "./sectionFour.scss";

const SectionFour = () => {
	return (
		<div className="section-four">
			<div className="container">
				<div className="section-four__inner">
					<aside className="section-four__aside">
						<BlockColumns />
					</aside>
					<div className="section-four__main">
						<div className="section-four__main-item">
							<MainSection sectionTitle="" />
						</div>
						<div className="section-four__main-item">
							<SectionPromo />
						</div>
					</div>
				</div>
			</div>
		</div>
	);
};

export default SectionFour;
