import React from "react";
import { Swiper, SwiperSlide } from "swiper/react";
import { Thumbs, Navigation, Lazy } from "swiper";
import { Link } from "react-router-dom";

import { useMockAPI } from "../../hooks";
import SlidePromo from "../slidePromo/SlidePromo";
import PromoThumbSlide from "../promoThumbSlide/PromoThumbSlide";

import "swiper/css";
import "swiper/css/lazy";
import "./sectionPromo.scss";

const SectionPromo = () => {
	const { loading, error, getSlidesData } = useMockAPI();
	const [promoData, setPromoData] = React.useState(null);

	React.useEffect(() => {
		getSlidesInfo();
	}, []);

	const getSlidesInfo = async () => {
		const request = await getSlidesData("promo-slides", 1, 4).then(setPromoData);
	};
	const [thumbsSwiper, setThumbsSwiper] = React.useState(null);

	return (
		<section className="section-promo">
			<div className="section-promo__header">
				<h2 className="section-promo__title title"></h2>
				<Link className="section-promo__more" to="/anypage">
					 
				</Link>
			</div>
			<div className="section-promo__body">
				{loading && !promoData && (
					<p style={{ textAlign: "center", padding: "2rem 0" }}>Loading our promos...</p>
				)}
				{!error && promoData && promoData.length && (
					<>
						<Swiper
							className="section-promo__promo-slider promo-slider"
							modules={[Thumbs, Lazy]}
							slidesPerView={1}
							spaceBetween={32}
							speed={1000}
							thumbs={{
								swiper: thumbsSwiper && !thumbsSwiper.destroyed ? thumbsSwiper : null,
								autoScrollOffset: 1,
							}}
							allowTouchMove={false}
							lazy={{
								enabled: true,
								loadPrevNext: true,
							}}
							watchSlidesProgress
						>
							{promoData.map((item) => (
								<SwiperSlide key={item.dateTime} className="promo-slider__slide">
									<SlidePromo {...item} />
								</SwiperSlide>
							))}
						</Swiper>
						<Swiper
							className="section-promo__promo-thumbs-slider promo-thumbs-slider"
							modules={[Thumbs, Navigation]}
							watchSlidesProgress
							onSwiper={setThumbsSwiper}
							navigation={{
								nextEl: ".promo-thumbs-slider__control-right",
								prevEl: ".promo-thumbs-slider__control-left",
							}}
							slidesPerView={"auto"}
							spaceBetween={16}
							speed={1000}
							slidesOffsetBefore={24}
							slidesOffsetAfter={24}
							breakpoints={{
								769: {
									slidesOffsetBefore: 0,
									slidesOffsetAfter: 0,
								},
							}}
						>
							{promoData.map((item) => (
								<SwiperSlide key={item.dateTime} className="promo-thumbs-slider__slide">
									<PromoThumbSlide {...item} />
								</SwiperSlide>
							))}
							<div slot="container-end">
								<div className="promo-thumbs-slider__controls swiper-buttons">
									<div className="promo-thumbs-slider__control-left disabled">
										<div className="swiper-button-prev">
											<svg
												width="16"
												height="16"
												viewBox="0 0 16 16"
												fill="none"
												xmlns="http://www.w3.org/2000/svg"
											>
												<g clipPath="url(#clip0_4344_1318)">
													<path
														d="M11.3334 -4.24386e-05C11.4211 -0.000549701 11.5081 0.0162667 11.5893 0.0494431C11.6705 0.0826195 11.7444 0.131504 11.8067 0.193292C11.8692 0.255267 11.9188 0.329 11.9526 0.41024C11.9865 0.491479 12.0039 0.578616 12.0039 0.666624C12.0039 0.754632 11.9865 0.841769 11.9526 0.923009C11.9188 1.00425 11.8692 1.07798 11.8067 1.13996L6.36004 6.58662C5.98551 6.96163 5.77513 7.46996 5.77513 7.99996C5.77513 8.52996 5.98551 9.03829 6.36004 9.41329L11.8067 14.86C11.9322 14.9855 12.0028 15.1558 12.0028 15.3333C12.0028 15.5108 11.9322 15.6811 11.8067 15.8066C11.6812 15.9322 11.5109 16.0027 11.3334 16.0027C11.1558 16.0027 10.9856 15.9322 10.86 15.8066L5.41337 10.36C5.10295 10.0503 4.85667 9.68248 4.68862 9.27752C4.52058 8.87255 4.43408 8.43841 4.43408 7.99996C4.43408 7.56151 4.52058 7.12737 4.68862 6.7224C4.85667 6.31743 5.10295 5.9496 5.41337 5.63996L10.86 0.193292C10.9223 0.131504 10.9962 0.0826195 11.0774 0.0494431C11.1587 0.0162667 11.2456 -0.000549701 11.3334 -4.24386e-05Z"
														fill="currentColor"
													/>
												</g>
												<defs>
													<clipPath id="clip0_4344_1318">
														<rect
															width="16"
															height="16"
															fill="white"
															transform="translate(16 16) rotate(180)"
														/>
													</clipPath>
												</defs>
											</svg>
										</div>
									</div>
									<div className="promo-thumbs-slider__control-right">
										<div className="swiper-button-next">
											<svg
												width="16"
												height="16"
												viewBox="0 0 16 16"
												fill="none"
												xmlns="http://www.w3.org/2000/svg"
											>
												<g clipPath="url(#clip0_1_3552)">
													<path
														d="M4.66663 16C4.57889 16.0005 4.49191 15.9837 4.41069 15.9506C4.32946 15.9174 4.25559 15.8685 4.19329 15.8067C4.13081 15.7447 4.08121 15.671 4.04737 15.5898C4.01352 15.5085 3.99609 15.4214 3.99609 15.3334C3.99609 15.2454 4.01352 15.1582 4.04737 15.077C4.08121 14.9958 4.13081 14.922 4.19329 14.86L9.63996 9.41338C10.0145 9.03837 10.2249 8.53004 10.2249 8.00004C10.2249 7.47004 10.0145 6.96171 9.63996 6.58671L4.19329 1.14004C4.06776 1.01451 3.99723 0.844243 3.99723 0.666709C3.99723 0.489174 4.06776 0.318911 4.19329 0.193376C4.31883 0.0678397 4.48909 -0.00268555 4.66663 -0.00268555C4.84416 -0.00268555 5.01442 0.0678397 5.13996 0.193376L10.5866 5.64004C10.897 5.94968 11.1433 6.31752 11.3114 6.72248C11.4794 7.12745 11.5659 7.56159 11.5659 8.00004C11.5659 8.43849 11.4794 8.87263 11.3114 9.2776C11.1433 9.68257 10.897 10.0504 10.5866 10.36L5.13996 15.8067C5.07767 15.8685 5.00379 15.9174 4.92256 15.9506C4.84134 15.9837 4.75436 16.0005 4.66663 16Z"
														fill="currentColor"
													/>
												</g>
												<defs>
													<clipPath id="clip0_1_3552">
														<rect width="16" height="16" fill="white" />
													</clipPath>
												</defs>
											</svg>
										</div>
									</div>
								</div>
							</div>
						</Swiper>
					</>
				)}
			</div>
		</section>
	);
};

export default SectionPromo;
