import React from "react";
import { Swiper, SwiperSlide } from "swiper/react";
import { Link } from "react-router-dom";

import ItemColumn from "../itemColumn/ItemColumn";
import LoadMoreButton from "../loadMoreButton/LoadMoreButton";

import "swiper/css";
import "./blockColumns.scss";
import img01 from "../../assets/images/section-four/columns/01.png";
import img02 from "../../assets/images/section-four/columns/02.png";
import img03 from "../../assets/images/section-four/columns/03.png";
import img04 from "../../assets/images/section-four/columns/04.png";

const columnsData = [
	{
		author: " ",
		profession: "",
		imageSrc: img01,
		profileUrl: "#",
		recentWorks: [
			{
				title: "      ",
				dateTime: "2022-08-04T15:15",
				url: "#",
			},
		],
	},
	{
		author: " ",
		profession: "",
		imageSrc: img02,
		profileUrl: "#",
		recentWorks: [
			{
				title: " - -",
				dateTime: "2022-07-04T17:14",
				url: "#",
			},
		],
	},
	{
		author: " ",
		profession: "",
		imageSrc: img03,
		profileUrl: "#",
		recentWorks: [
			{
				title: " . '   ",
				dateTime: "2022-08-02T21:01",
				url: "#",
			},
		],
	},
	{
		author: " ",
		profession: "",
		imageSrc: img04,
		profileUrl: "#",
		recentWorks: [
			{
				title: "   .  ",
				dateTime: "2022-08-04T18:38",
				url: "#",
			},
		],
	},
];

const BlockColumns = () => {
	return (
		<div className="block-columns">
			<div className="block-columns__header">
				<h4 className="block-columns__title"></h4>
				<Link to="/anypage" className="block-columns__link">
					 
				</Link>
			</div>
			<div className="block-columns__body">
				{columnsData && columnsData.length && (
					<>
						<ul className="block-columns__list">
							{columnsData.map((item) => (
								<li key={item.author} className="block-columns__item">
									<ItemColumn {...item} />
								</li>
							))}
						</ul>
						<Swiper
							className="block-columns__slider block-columns-slider"
							wrapperTag="ul"
							slidesPerView={"auto"}
							slidesPerGroup={1}
							spaceBetween={24}
							speed={1000}
							slidesOffsetBefore={24}
							slidesOffsetAfter={24}
						>
							{columnsData.map((item) => (
								<SwiperSlide key={item.author} className="block-columns-slider__slide" tag="li">
									<ItemColumn className="block-columns-slider__slide" {...item} />
								</SwiperSlide>
							))}
						</Swiper>
					</>
				)}
				<LoadMoreButton className="block-columns__more" title=" " url="#" withoutIcon />
			</div>
		</div>
	);
};

export default BlockColumns;
