import React from "react";
import { Link } from "react-router-dom";

import { getFormattedTime } from "../../services";

import "./itemColumn.scss";

const ItemColumn = ({ author, profession, imageSrc, profileUrl, recentWorks }) => {
	return (
		<div className="item-column">
			<div className="item-column__top">
				<Link className="item-column__photo" to="/anypage" aria-label={`    - ${author}`}>
					<img src={imageSrc} alt={author} />
				</Link>
				<div className="item-column__author">
					<Link
						className="item-column__name"
						to="/anypage"
						aria-label={`    - ${author}`}
					>
						{author}
					</Link>
					<p className="item-column__label">{profession}</p>
				</div>
			</div>
			{recentWorks && recentWorks.length && (
				<>
					<Link className="item-column__article" to="/anypage">
						{recentWorks[0].title}
					</Link>
					<time className="item-column__time" dateTime={recentWorks[0].dateTime}>
						{getFormattedTime(recentWorks[0].dateTime)}
					</time>
				</>
			)}
		</div>
	);
};

export default ItemColumn;
