import React from "react";
import { Link } from "react-router-dom";

import { useMockAPI, useScrollbar, useMatchMedia } from "../../hooks";
import CustomTablist from "../customTablist/CustomTablist";
import BlockNews from "../blockNews/BlockNews";
import ItemArticle from "../itemArticle/ItemArticle";
import LoadMoreButton from "../loadMoreButton/LoadMoreButton";
import BlockNewsSkeleton from "../blockNewsSkeleton/BlockNewsSkeleton";

import "overlayscrollbars/css/OverlayScrollbars.css";
import "./allNews.scss";

const AllNews = () => {
	const { loading, error, getNewsQuantity, getNews } = useMockAPI();
	const [selectedTab, setSelectedTab] = React.useState("");
	const [news, setNews] = React.useState(null);
	const [pages, setPages] = React.useState({
		currentPageNumber: 1,
		newsPerPage: 10,
		availablePagesQuantity: 0,
		isLastPage: false,
	});
	const allNewsContentWrapperRef = React.useRef(null);
	const { isMobile, isTablet, isDesktop } = useMatchMedia();
	const [sectionHeight, setSectionHeight] = React.useState(null);

	const hasScroll = sectionHeight && news && news.length > 9;

	useScrollbar(allNewsContentWrapperRef, hasScroll);

	React.useEffect(() => {
		if (isMobile) {
			setSectionHeight(null);
		}

		if (isTablet) {
			setSectionHeight(62.75);
		}

		if (isDesktop) {
			setSectionHeight(91.8);
		}
	}, [isMobile, isTablet, isDesktop]);

	React.useEffect(() => {
		getPagesQuantity();
		getRecentNews();
	}, [selectedTab]);

	const onChangeTab = (tab) => {
		setSelectedTab(tab);
		setPages((state) => ({ ...state, currentPageNumber: 1 }));
		setNews(null);
	};

	const getPagesQuantity = async () => {
		const newsQuantity = await getNewsQuantity(selectedTab !== "" ? selectedTab : null).then((quantity) => {
			const pagesQuantity = Math.ceil(quantity / pages.newsPerPage);

			setPages((state) => ({
				...state,
				availablePagesQuantity: pagesQuantity,
				isLastPage: state.currentPageNumber === pagesQuantity,
			}));
		});
	};

	const getRecentNews = async () => {
		const news = await getNews(
			selectedTab !== "" ? selectedTab : null,
			pages.currentPageNumber,
			pages.newsPerPage
		).then(setNews);
	};

	const loadMoreNews = async () => {
		const nextPageNumber = pages.currentPageNumber + 1;

		if (nextPageNumber <= pages.availablePagesQuantity) {
			const news = await getNews(selectedTab, nextPageNumber, pages.newsPerPage).then((response) => {
				setNews((state) => [...state, ...response]);
				setPages((state) => ({
					...state,
					currentPageNumber: nextPageNumber,
					isLastPage: nextPageNumber === state.availablePagesQuantity,
				}));
			});
		}
	};

	return (
		<div className="all-news" style={{ maxHeight: sectionHeight ? `${sectionHeight}rem` : "unset" }}>
			<div className="all-news__header">
				<p className="all-news__title"> </p>
				<Link to="/anypage" className="all-news__more">
					
				</Link>
			</div>
			<div className="all-news__filters">
				<CustomTablist
					tablist={["", "", ""]}
					selectedTab={selectedTab}
					changeSelectedTab={onChangeTab}
				/>
			</div>
			<div
				className="all-news__scroll"
				ref={allNewsContentWrapperRef}
				style={{ flexGrow: loading ? 1 : 0 }}
				data-lastpage={pages.isLastPage}
			>
				<div className="all-news__content">
					{!news &&
						loading &&
						new Array(pages.newsPerPage).fill(null).map((item, i) => <BlockNewsSkeleton key={i} />)}
					{!error && news && news.length && (
						<ul className="all-news__content-list">
							{news.map(({ type, id, ...newsItem }) =>
								type === "news" ? (
									<BlockNews key={id} className="all-news__content-item" size="large" {...newsItem} />
								) : (
									<ItemArticle key={id} className="all-news__content-item" {...newsItem} />
								)
							)}
						</ul>
					)}
				</div>
			</div>
			{!pages.isLastPage && (
				<LoadMoreButton
					className="all-news__button"
					title=" "
					onClickButton={loadMoreNews}
					isLoading={loading}
				/>
			)}
		</div>
	);
};

export default AllNews;
