import React from "react";

import { useMockAPI } from "../../hooks";
import BlockNews from "../blockNews/BlockNews";
import BlockNewsSkeleton from "../blockNewsSkeleton/BlockNewsSkeleton";
import MoreButton from "../moreButton/MoreButton";

import "./sectionNewsMobile.scss";

const SectionNewsMobile = () => {
	const { loading, error, getItems } = useMockAPI();
	const [news, setNews] = React.useState(null);

	React.useEffect(() => {
		getNews();
	}, []);

	const getNews = async () => {
		const request = await getItems("politic-news", 1, 10).then(setNews);
	};
	return (
		<div className="section-news-mobile">
			{loading && !news && (
				<ul className="section-news-mobile__list" aria-hidden>
					{new Array(6).fill(null).map((item, i) => (
						<BlockNewsSkeleton key={i} />
					))}
				</ul>
			)}
			{!error && news && news.length && (
				<ul className="section-news-mobile__list">
					{news.map((item) => (
						<BlockNews key={item.title + item.dateTime} className="section-news-mobile__item" {...item} />
					))}
				</ul>
			)}
			<MoreButton className="section-news-mobile__more" label="" url="#" />
		</div>
	);
};

export default SectionNewsMobile;
