import React from "react";
import { useSwiper } from "swiper/react";

import "./regionsSliderTablist.scss";

const RegionsSliderTablist = ({ className, tablist, selectedTab, changeSelectedTab }) => {
	const swiper = useSwiper();

	const selectTab = (event, name, index) => {
		changeSelectedTab(name);
		swiper.slideTo(index, 1000);
	};

	const handleKeyDown = (event, name, index) => {
		if (event.code === "Space" || event.code === "Enter") {
			event.preventDefault();
			selectTab(name, index);
		}
	};
	return (
		tablist &&
		tablist.length && (
			<ul className={"tablist" + (className ? ` ${className}` : "")} role="tablist">
				{tablist.map((tabHeader, index) => (
					<li
						key={tabHeader}
						className={"tab" + (tabHeader === selectedTab ? " selected" : "")}
						tabIndex="0"
						role="tab"
						aria-selected={selectedTab === tabHeader}
						onClick={(e) => selectTab(e, tabHeader, index)}
						onKeyDown={(e) => handleKeyDown(e, tabHeader, index)}
					>
						{tabHeader}
					</li>
				))}
			</ul>
		)
	);
};

export default RegionsSliderTablist;
