import React from 'react'

const useMockAPI = () => {
    const [loading, setLoading] = React.useState(false);
    const [error, setError] = React.useState(false);

    const getNews = async (type = null, page = 1, itemsPerPage = 10) => {
        setLoading(true);

        let requestURL = '';

        switch (type) {
            case '':
                requestURL = `https://636cb6c191576e19e312d1ec.mockapi.io/news?page=${page}&limit=${itemsPerPage}&sortBy=dateTime&order=desc&type=news`
                break;
            case "":
                requestURL = `https://636cb6c191576e19e312d1ec.mockapi.io/news?page=${page}&limit=${itemsPerPage}&sortBy=dateTime&order=desc&type=article`
                break;
            default:
                requestURL = `https://636cb6c191576e19e312d1ec.mockapi.io/news?page=${page}&limit=${itemsPerPage}&sortBy=dateTime&order=desc`
                break;
        }

        const result = await fetch(requestURL)
            .then(response => {
                if (response.ok) {
                    setLoading(false);
                    return response.json();
                } else {
                    throw new Error('News or articles have not been loaded. Please, try again later')
                }
            })
            .catch((e) => {
                setLoading(false);
                setError(true);
                return []
            })
        return result;
    }

    const getNewsQuantity = async (type = null) => {
        let requestURL = '';

        switch (type) {
            case '':
                requestURL = `https://636cb6c191576e19e312d1ec.mockapi.io/news?type=news`
                break;
            case "":
                requestURL = `https://636cb6c191576e19e312d1ec.mockapi.io/news?type=article`
                break;
            default:
                requestURL = `https://636cb6c191576e19e312d1ec.mockapi.io/news`
                break;
        }


        const quantity = await fetch(requestURL)
            .then(response => {
                if (response.ok) {
                    return response.json();
                } else {
                    throw new Error('Error happened while fetching news quantity')
                }
            })
            .then(response => response.length)
            .catch((e) => {
                setError(true);
                return 0
            })

        return quantity;
    };

    const getItems = async (endpoint, page, itemsPerPage) => {
        setLoading(true);

        let requestURL = '';

        switch (endpoint) {
            case 'articles':
                requestURL = `https://636cb6c191576e19e312d1ec.mockapi.io/articles?page=${page}&limit=${itemsPerPage}&sortBy=dateTime&order=desc&type=news`
                break;
            case "politic-news":
                requestURL = `https://636cb6c191576e19e312d1ec.mockapi.io/politic-news?page=${page}&limit=${itemsPerPage}&sortBy=dateTime&order=desc`
                break;
            case "politic-articles":
                requestURL = `https://636cb6c191576e19e312d1ec.mockapi.io/politic-articles?page=${page}&limit=${itemsPerPage}&sortBy=dateTime&order=desc`
                break;
            default:
                requestURL = `https://636cb6c191576e19e312d1ec.mockapi.io/news?page=${page}&limit=${itemsPerPage}&sortBy=dateTime&order=desc`
                break;
        }

        const result = await fetch(requestURL)
            .then(response => {
                if (response.ok) {
                    setLoading(false);
                    return response.json();
                } else {
                    throw new Error('News or articles have not been loaded. Please, try again later')
                }
            })
            .catch((e) => {
                setLoading(false);
                setError(true);
                return []
            })
        return result;
    }

    const getItemsQuantity = async (endpoint) => {
        let requestURL = '';

        switch (endpoint) {
            case 'articles':
                requestURL = 'https://636cb6c191576e19e312d1ec.mockapi.io/articles'
                break;
            case "politic-news":
                requestURL = 'https://636cb6c191576e19e312d1ec.mockapi.io/politic-news'
                break;
            case "politic-articles":
                requestURL = 'https://636cb6c191576e19e312d1ec.mockapi.io/politic-articles'
                break;
            default:
                requestURL = 'https://636cb6c191576e19e312d1ec.mockapi.io/news'
                break;
        }


        const quantity = await fetch(requestURL)
            .then(response => {
                if (response.ok) {
                    return response.json();
                } else {
                    throw new Error('Error happened while fetching news quantity')
                }
            })
            .then(response => response.length)
            .catch((e) => {
                setError(true);
                return 0
            })

        return quantity;
    }

    const getSlidesData = async (endpoint, page, itemsPerPage) => {
        setLoading(true);

        const result = await fetch(`https://636cc86dab4814f2b26e197a.mockapi.io/${endpoint}?page=${page}&limit=${itemsPerPage}&sortBy=dateTime&order=desc`)
            .then(response => {
                if (response.ok) {
                    setLoading(false);
                    return response.json();
                } else {
                    throw new Error('Slides data has not been loaded. Please, try again later')
                }
            })
            .catch((e) => {
                setLoading(false);
                setError(true);
                return []
            })
        return result;
    }

    return {
        loading,
        error,
        getNewsQuantity,
        getNews,
        getItems,
        getItemsQuantity,
        getSlidesData
    }
}

export default useMockAPI;