import React from "react";

import { getFormattedTime } from "../../services";

import "./promoThumbSlide.scss";

const PromoThumbSlide = ({ title, dateTime }) => {
	return (
		<div className="promo-thumb-slide">
			<time className="promo-thumb-slide__time" dateTime={dateTime}>
				{getFormattedTime(dateTime)}
			</time>
			<p className="promo-thumb-slide__title">{title}</p>
		</div>
	);
};

export default PromoThumbSlide;
