import React from "react";
import { Link } from "react-router-dom";

import { useMatchMedia, useMockAPI } from "../../hooks";
import { getFormattedTime } from "../../services";
import EditorsChoiceSkeleton from "../editorsChoiceSkeleton/EditorsChoiceSkeleton";
import LoadMoreButton from "../loadMoreButton/LoadMoreButton";

import "./editorsChoice.scss";

const EditorsChoice = () => {
	const { isMobile } = useMatchMedia();
	const { loading, error, getItems, getItemsQuantity } = useMockAPI();
	const [articles, setArticles] = React.useState(null);
	const [pages, setPages] = React.useState({
		currentPageNumber: 1,
		articlesPerPage: 6,
		availablePagesQuantity: 0,
		isLastPage: false,
	});

	React.useEffect(() => {
		getPagesQuantity();
		getArticles();
	}, []);

	const getArticles = async () => {
		const request = await getItems("articles", pages.currentPageNumber, pages.articlesPerPage).then(setArticles);
	};

	const getPagesQuantity = async () => {
		const request = await getItemsQuantity("articles").then((quantity) => {
			const pagesQuantity = Math.ceil(quantity / pages.articlesPerPage);

			setPages((state) => ({
				...state,
				availablePagesQuantity: pagesQuantity,
				isLastPage: state.currentPageNumber === pagesQuantity,
			}));
		});
	};

	const loadMoreArticles = async () => {
		const nextPageNumber = pages.currentPageNumber + 1;

		if (nextPageNumber <= pages.availablePagesQuantity) {
			const request = await getItems("articles", nextPageNumber, pages.articlesPerPage).then((response) => {
				setArticles((state) => [...state, ...response]);
				setPages((state) => ({
					...state,
					currentPageNumber: nextPageNumber,
					isLastPage: nextPageNumber === state.availablePagesQuantity,
				}));
			});
		}
	};

	return (
		<div className="editors-choice">
			<div className="editors-choice__header">
				<h2 className="editors-choice__title title"> </h2>
			</div>
			{loading && !articles && <EditorsChoiceSkeleton />}
			{!error && articles && articles.length && (
				<ul className="editors-choice__list">
					{articles.map(({ title, dateTime, imageSrc, imageAlt, url }) => (
						<li key={title + dateTime} className="editors-choice__item">
							<Link
								className="editors-choice__link"
								to="/anypage"
								aria-label="    "
							>
								<img src={imageSrc} alt={imageAlt} className="editors-choice__image" />
							</Link>
							<time className="editors-choice__time" dateTime={dateTime}>
								{getFormattedTime(dateTime)}
							</time>
							<Link
								className="editors-choice__item-title"
								to="/anypage"
								aria-label="    "
							>
								{title}
							</Link>
						</li>
					))}
				</ul>
			)}
			{isMobile && !pages.isLastPage && (
				<LoadMoreButton
					className="editors-choice__button"
					title=" "
					isLoading={loading}
					onClickButton={loadMoreArticles}
				/>
			)}
		</div>
	);
};

export default EditorsChoice;
