import React from "react";
import { SwitchTransition, CSSTransition } from "react-transition-group";

import CustomTablist from "../customTablist/CustomTablist";
import SectionNewsMobile from "../sectionNewsMobile/SectionNewsMobile";
import SectionArticlesMobile from "../sectionArticlesMobile/SectionArticlesMobile";

import "./newsArticlesMobile.scss";

const NewsArticlesMobile = () => {
	const [selectedTab, setSelectedTab] = React.useState("");
	const [toggler, setToggler] = React.useState(true);

	const changeSelectedTab = (tabName) => {
		setSelectedTab(tabName);
		setToggler((state) => !state);
	};

	return (
		<div className="news-articles-mobile">
			<CustomTablist
				className="news-articles-mobile__controls"
				tablist={["", ""]}
				selectedTab={selectedTab}
				changeSelectedTab={changeSelectedTab}
			/>
			<div className="news-articles-mobile__body">
				<SwitchTransition mode="out-in">
					<CSSTransition key={toggler} classNames="news-articles-mobile-animation" timeout={500}>
						{toggler ? <SectionNewsMobile /> : <SectionArticlesMobile />}
					</CSSTransition>
				</SwitchTransition>
			</div>
		</div>
	);
};

export default NewsArticlesMobile;
