import React from "react";
import { Link } from "react-router-dom";
import Accordion from "@mui/material/Accordion";
import AccordionSummary from "@mui/material/AccordionSummary";
import AccordionDetails from "@mui/material/AccordionDetails";

import "./menuItemAccordion.scss";

const AccordionIcon = () => {
	return (
		<svg
			className="menu__icon"
			width="17"
			height="17"
			viewBox="0 0 17 17"
			fill="none"
			xmlns="http://www.w3.org/2000/svg"
		>
			<g clipPath="url(#clip0_1_4267)">
				<path
					d="M0.499958 5.08362C0.499451 4.99588 0.516267 4.9089 0.549443 4.82768C0.58262 4.74645 0.631504 4.67258 0.693292 4.61028C0.755267 4.5478 0.829 4.4982 0.91024 4.46436C0.991479 4.43051 1.07862 4.41309 1.16662 4.41309C1.25463 4.41309 1.34177 4.43051 1.42301 4.46436C1.50425 4.4982 1.57798 4.5478 1.63996 4.61028L7.08662 10.057C7.46162 10.4315 7.96996 10.6419 8.49996 10.6419C9.02996 10.6419 9.53829 10.4315 9.91329 10.057L15.36 4.61028C15.4855 4.48475 15.6558 4.41422 15.8333 4.41422C16.0108 4.41422 16.1811 4.48475 16.3066 4.61028C16.4322 4.73582 16.5027 4.90608 16.5027 5.08362C16.5027 5.26115 16.4322 5.43142 16.3066 5.55695L10.86 11.0036C10.5503 11.314 10.1825 11.5603 9.77752 11.7284C9.37255 11.8964 8.93841 11.9829 8.49996 11.9829C8.06151 11.9829 7.62737 11.8964 7.2224 11.7284C6.81743 11.5603 6.4496 11.314 6.13996 11.0036L0.693292 5.55695C0.631504 5.49466 0.58262 5.42078 0.549443 5.33956C0.516267 5.25833 0.499451 5.17136 0.499958 5.08362Z"
					fill="#2D2D2D"
				/>
			</g>
			<defs>
				<clipPath id="clip0_1_4267">
					<rect width="16" height="16" fill="white" transform="translate(16.5 0.416992) rotate(90)" />
				</clipPath>
			</defs>
		</svg>
	);
};

const MenuItemAccordion = ({ title, links, toggleMenu }) => {
	return (
		<Accordion className="menu__accordion accordion" square disableGutters>
			<AccordionSummary
				className="accordion__header menu__title"
				expandIcon={<AccordionIcon />}
				aria-controls={`${title}-content`}
				id={`${title}-header`}
			>
				{title}
			</AccordionSummary>
			<AccordionDetails className="accordion__body">
				<ul className="menu__list">
					{links.length &&
						links.map(({ label, url }) => (
							<li className="menu__item" key={label}>
								<Link className="menu__link" to="/anypage" onClick={toggleMenu}>
									{label}
								</Link>
							</li>
						))}
				</ul>
			</AccordionDetails>
		</Accordion>
	);
};

export default MenuItemAccordion;
