import React from "react";

import { useMockAPI } from "../../hooks";
import BlockNews from "../blockNews/BlockNews";
import BlockNewsSkeleton from "../blockNewsSkeleton/BlockNewsSkeleton";
import MoreButton from "../moreButton/MoreButton";

import "./sectionNews.scss";

const SectionNews = ({ title }) => {
	const { loading, error, getItems } = useMockAPI();
	const [news, setNews] = React.useState(null);

	React.useEffect(() => {
		getNews();
	}, []);

	const getNews = async () => {
		const request = await getItems("politic-news", 1, 6).then(setNews);
	};
	return (
		<div className="section-news">
			<p className="section-news__title">{title}</p>
			<div className="section-news__body">
				{loading && !news && (
					<ul className="section-news__list" aria-hidden>
						{new Array(4).fill(null).map((item, i) => (
							<BlockNewsSkeleton key={i} />
						))}
					</ul>
				)}
				{!error && news && news.length && (
					<ul className="section-news__list">
						{news.map((item) => (
							<BlockNews key={item.title + item.dateTime} className="section-news__item" {...item} />
						))}
					</ul>
				)}
				<MoreButton label="" className="section-news__more" url="#" />
			</div>
		</div>
	);
};

export default SectionNews;
