import React from "react";
import Skeleton from "@mui/material/Skeleton";

import "./blockArticleSkeleton.scss";

const BlockArticleSkeleton = () => {
	return (
		<div className="block-article-skeleton" aria-hidden>
			<Skeleton className="block-article-skeleton__image" variant="rectangular" sx={{ marginBottom: "0.7rem" }} />
			<Skeleton variant="rectangular" width={"30%"} height={16} sx={{ marginBottom: "0.5rem" }} />
			<Skeleton variant="text" sx={{ fontSize: "1.375rem" }} />
			<Skeleton variant="text" sx={{ fontSize: "1rem" }} />
			<Skeleton variant="text" sx={{ fontSize: "1rem" }} />
		</div>
	);
};

export default BlockArticleSkeleton;
