import React from "react";
import classNames from "classnames";
import { Link } from "react-router-dom";

import "./blockNews.scss";

const BlockNews = ({ className, title, label, dateTime, url, type, size }) => {
	const itemClassnames = classNames({
		[className]: className,
		"block-news": true,
		"block-news--with-photo": label && label === "  ",
		"block-news--hot": label && label === " ",
		"block-news--video": label && label === "  ",
		"block-news--online": label && label === "",
		"block-news--bigger-padding": size && size === "large",
	});

	const hours = new Date(dateTime).getHours();
	const minutes = new Date(dateTime).getMinutes();
	const time = `${hours > 9 ? hours : `0${hours}`}:${minutes > 9 ? minutes : `0${minutes}`}`;

	return (
		<li className={itemClassnames}>
			{label && <p className="block-news__label">{label}</p>}
			<Link to="/anypage" className="block-news__link">
				<time dateTime={dateTime}>{time}</time>
				{title}
			</Link>
		</li>
	);
};

export default BlockNews;
