import React from "react";
import Skeleton from "@mui/material/Skeleton";

import "./blockNewsSkeleton.scss";

const BlockNewsSkeleton = () => {
	return (
		<div className="block-news-skeleton" aria-hidden>
			{/* <Skeleton variant="text" sx={{ fontSize: "1rem", marginBottom: "0.8rem" }} /> */}
			<Skeleton
				variant="rectangular"
				width={"40%"}
				hight={"0.8rem"}
				sx={{ marginBottom: "0.8rem", borderRadius: 4 }}
			/>
			<Skeleton variant="rectangular" width={"100%"} height={70} />
		</div>
	);
};

export default BlockNewsSkeleton;
