import React from "react";
import { FocusTrap } from "@mui/base";
import { CSSTransition } from "react-transition-group";

import { useMatchMedia } from "../../hooks";

import "./topHeaderLanguages.scss";

const TopHeaderLanguages = ({ selectedItem, items }) => {
	const [selectedLanguage, setSelectedLanguage] = React.useState(selectedItem || "UA");
	const [isPopupOpened, setPopupOpened] = React.useState(false);
	const { isMobile } = useMatchMedia();
	const selectLanguageRef = React.useRef(null);

	const onClickOutside = React.useCallback((event) => {
		if (event.target.closest(".select-language") !== selectLanguageRef.current) {
			togglePopup();
		}
	}, []);

	React.useEffect(() => {
		if (isPopupOpened) {
			window.addEventListener("click", onClickOutside);

			return () => {
				window.removeEventListener("click", onClickOutside);
			};
		}
	}, [isPopupOpened, onClickOutside]);

	const togglePopup = () => {
		setPopupOpened((state) => !state);
	};

	const changeLanguage = (event, newLanguage) => {
		event.preventDefault();
		setSelectedLanguage(newLanguage);

		if (isMobile) {
			togglePopup();
		}
	};

	return isMobile ? (
		<div className="top-header__select-language select-language" ref={selectLanguageRef}>
			<button className="select-language__button" onClick={togglePopup}>
				<span className="select-language__label">{selectedLanguage}</span>
				<svg
					className="select-language__icon"
					width="10"
					height="6"
					viewBox="0 0 10 6"
					fill="none"
					xmlns="http://www.w3.org/2000/svg"
				>
					<path
						d="M1.27602 0H8.72402C8.85586 2.81496e-05 8.98472 0.0391426 9.09433 0.112399C9.20393 0.185655 9.28936 0.289764 9.33981 0.411564C9.39025 0.533364 9.40346 0.667386 9.37774 0.796687C9.35203 0.925989 9.28856 1.04477 9.19535 1.138L5.47135 4.862C5.34634 4.98698 5.1768 5.05719 5.00002 5.05719C4.82325 5.05719 4.65371 4.98698 4.52869 4.862L0.804688 1.138C0.711482 1.04477 0.648011 0.925989 0.622299 0.796687C0.596588 0.667386 0.60979 0.533364 0.660237 0.411564C0.710683 0.289764 0.79611 0.185655 0.905716 0.112399C1.01532 0.0391426 1.14419 2.81496e-05 1.27602 0Z"
						fill="currentColor"
					/>
				</svg>
			</button>
			<CSSTransition in={isPopupOpened} timeout={500} classNames="language-animation" mountOnEnter unmountOnExit>
				<div className="select-language__body">
					<FocusTrap open={isPopupOpened}>
						<ul className="select-language__list" tabIndex={-1}>
							{items.length &&
								items.map((item) => (
									<li
										className={
											"select-language__item" + (selectedLanguage === item ? " selected" : "")
										}
										key={item}
									>
										<button onClick={(e) => changeLanguage(e, item)}>{item}</button>
									</li>
								))}
						</ul>
					</FocusTrap>
				</div>
			</CSSTransition>
		</div>
	) : (
		<ul className="top-header__languages-list">
			{items.length &&
				items.map((item) => (
					<li
						className={"top-header__language-item" + (selectedLanguage === item ? " selected" : "")}
						key={item}
					>
						<button
							className="top-header__language-link"
							aria-label={`   ${item}`}
							onClick={(e) => changeLanguage(e, item)}
						>
							{item}
						</button>
					</li>
				))}
		</ul>
	);
};

export default TopHeaderLanguages;
