import React from "react";
import { Link } from "react-router-dom";

import ItemVideo from "../itemVideo/ItemVideo";
import MoreButton from "../moreButton/MoreButton";
import LoadMoreButton from "../loadMoreButton/LoadMoreButton";

import "./sectionVideos.scss";
import img01 from "../../assets/images/section-three/01.jpg";
import img02 from "../../assets/images/section-three/02.jpg";
import img03 from "../../assets/images/section-three/03.jpg";
import img04 from "../../assets/images/section-three/04.jpg";
import img05 from "../../assets/images/section-three/05.jpg";
import img01webp from "../../assets/images/section-three/01.webp";
import img02webp from "../../assets/images/section-three/02.webp";
import img03webp from "../../assets/images/section-three/03.webp";
import img04webp from "../../assets/images/section-three/04.webp";
import img05webp from "../../assets/images/section-three/05.webp";

const videos = [
	{
		title: '"  ":  ,     ',
		dateTime: "2022-08-05T11:00",
		url: "#",
		image: {
			webp: img01webp,
			src: img01,
			alt: "   --",
		},
	},
	{
		title: ' ""       . ',
		dateTime: "2022-08-05T13:02",
		url: "#",
		image: {
			webp: img02webp,
			src: img02,
			alt: "",
		},
	},
	{
		title: '"  ":         ()',
		dateTime: "2022-08-06T10:20",
		url: "#",
		image: {
			webp: img03webp,
			src: img03,
			alt: "  ",
		},
	},
	{
		title: "    ,     ()",
		dateTime: "2022-08-05T21:00",
		url: "#",
		image: {
			webp: img04webp,
			src: img04,
			alt: "  -  ",
		},
	},
	{
		title: '    " ",     ()',
		dateTime: "2022-08-01T01:30",
		url: "#",
		image: {
			webp: img05webp,
			src: img05,
			alt: " ",
		},
	},
];

const SectionVideos = () => {
	return (
		<section className="section-three">
			<div className="container">
				<div className="section-three__inner">
					<div className="section-three__header">
						<h2 className="section-three__title title"></h2>
						<MoreButton className="section-three__more" label=" " url="#" />
						<Link to="/anypage" className="section-three__more-videos">
							 
						</Link>
					</div>
					<div className="section-three__content">
						{videos && videos.length && (
							<ul className="section-three__list">
								{videos.map((video, index) => (
									<ItemVideo
										key={video.title}
										{...video}
										className="section-three__item"
										isLarge={index === 0}
									/>
								))}
							</ul>
						)}
						<LoadMoreButton className={"section-three__button"} title={""} withoutIcon />
					</div>
				</div>
			</div>
		</section>
	);
};

export default SectionVideos;
