import React from "react";
import { Swiper, SwiperSlide } from "swiper/react";
import { Navigation, Pagination, Thumbs, Lazy } from "swiper";
import CircularProgress from "@mui/material/CircularProgress";
import { Link } from "react-router-dom";

import { useMockAPI, useWebPSupportCheck } from "../../hooks";

import "swiper/css";
import "swiper/css/navigation";
import "swiper/css/pagination";
import "swiper/css/thumbs";
import "swiper/css/lazy";
import "./mainSlider.scss";

const MainSlider = () => {
	const supportsWebP = useWebPSupportCheck();
	const { loading, error, getSlidesData } = useMockAPI();
	const [slidesData, setSlidesData] = React.useState(null);

	React.useEffect(() => {
		getSlidesInfo();
	}, []);

	const getSlidesInfo = async () => {
		const request = await getSlidesData("main-slides", 1, 5).then(setSlidesData);
	};
	const [thumbsSwiper, setThumbsSwiper] = React.useState(null);
	const prevButtonRef = React.useRef(null);
	const nextButtonRef = React.useRef(null);
	return (
		<>
			{loading && !slidesData && (
				<div className="main-slider-skeleton">
					<CircularProgress />
				</div>
			)}
			{!error && slidesData && slidesData.length && (
				<>
					<Swiper
						thumbs={{
							swiper: thumbsSwiper && !thumbsSwiper.destroyed ? thumbsSwiper : null,
							autoScrollOffset: 1,
						}}
						modules={[Navigation, Pagination, Thumbs, Lazy]}
						slidesPerView={1}
						spaceBetween={24}
						speed={1000}
						allowTouchMove={false}
						className="first-screen__slider main-slider"
						pagination={{
							el: ".main-slider__pagination",
							type: "fraction",
						}}
						navigation={{
							nextEl: ".main-slider__button--next",
							prevEl: ".main-slider__button--prev",
						}}
						preloadImages={false}
						lazy={{
							enabled: true,
							loadPrevNext: true,
						}}
						watchSlidesProgress={true}
						breakpoints={{
							768: {
								thumbs: {
									autoScrollOffset: 2,
								},
							},
						}}
					>
						{slidesData.map(({ title, url, image, id }) => (
							<SwiperSlide
								key={id}
								tag={Link}
								className="main-slider__slide swiper-slide swiper-lazy"
								to="/anypage"
								data-background={supportsWebP ? image.webp : image.src}
							>
								<div className="swiper-lazy-preloader"></div>
								<p className="main-slider__slide-text">{title}</p>
							</SwiperSlide>
						))}
						<div className="main-slider__controls" slot="container-end">
							<div className="main-slider__buttons">
								<div
									className="main-slider__button main-slider__button--prev swiper-button-prev"
									ref={prevButtonRef}
								>
									<svg
										width="40"
										height="40"
										viewBox="0 0 40 40"
										fill="none"
										xmlns="http://www.w3.org/2000/svg"
									>
										<g clipPath="url(#clip0_4202_901)">
											<path
												d="M21.4991 26.0001C21.5649 26.0005 21.6301 25.9879 21.691 25.963C21.7519 25.9381 21.8073 25.9014 21.8541 25.8551C21.9009 25.8086 21.9381 25.7533 21.9635 25.6924C21.9889 25.6315 22.002 25.5661 22.002 25.5001C22.002 25.4341 21.9889 25.3687 21.9635 25.3078C21.9381 25.2469 21.9009 25.1916 21.8541 25.1451L17.7691 21.0601C17.4882 20.7788 17.3304 20.3976 17.3304 20.0001C17.3304 19.6026 17.4882 19.2213 17.7691 18.9401L21.8541 14.8551C21.9482 14.7609 22.0011 14.6332 22.0011 14.5001C22.0011 14.3669 21.9482 14.2392 21.8541 14.1451C21.7599 14.0509 21.6322 13.998 21.4991 13.998C21.3659 13.998 21.2382 14.0509 21.1441 14.1451L17.0591 18.2301C16.8262 18.4623 16.6415 18.7382 16.5155 19.0419C16.3895 19.3457 16.3246 19.6713 16.3246 20.0001C16.3246 20.3289 16.3895 20.6545 16.5155 20.9583C16.6415 21.262 16.8262 21.5379 17.0591 21.7701L21.1441 25.8551C21.1908 25.9014 21.2462 25.9381 21.3071 25.963C21.368 25.9879 21.4333 26.0005 21.4991 26.0001Z"
												fill="currentColor"
											/>
										</g>
										<rect
											x="-0.5"
											y="0.5"
											width="39"
											height="39"
											rx="19.5"
											transform="matrix(-1 0 0 1 39 0)"
											stroke="currentColor"
										/>
										<defs>
											<clipPath id="clip0_4202_901">
												<rect
													width="12"
													height="12"
													fill="currentColor"
													transform="matrix(-1 0 0 1 25 14)"
												/>
											</clipPath>
										</defs>
									</svg>
								</div>
								<div className="main-slider__pagination swiper-pagination"></div>
								<div
									className="main-slider__button main-slider__button--next swiper-button-next"
									ref={nextButtonRef}
								>
									<svg
										width="40"
										height="40"
										viewBox="0 0 40 40"
										fill="none"
										xmlns="http://www.w3.org/2000/svg"
									>
										<g clipPath="url(#clip0_4202_908)">
											<path
												d="M18.5009 26.0001C18.4351 26.0005 18.3699 25.9879 18.309 25.963C18.2481 25.9381 18.1927 25.9014 18.1459 25.8551C18.0991 25.8086 18.0619 25.7533 18.0365 25.6924C18.0111 25.6315 17.998 25.5661 17.998 25.5001C17.998 25.4341 18.0111 25.3687 18.0365 25.3078C18.0619 25.2469 18.0991 25.1916 18.1459 25.1451L22.2309 21.0601C22.5118 20.7788 22.6696 20.3976 22.6696 20.0001C22.6696 19.6026 22.5118 19.2213 22.2309 18.9401L18.1459 14.8551C18.0518 14.7609 17.9989 14.6332 17.9989 14.5001C17.9989 14.3669 18.0518 14.2392 18.1459 14.1451C18.2401 14.0509 18.3678 13.998 18.5009 13.998C18.6341 13.998 18.7618 14.0509 18.8559 14.1451L22.9409 18.2301C23.1738 18.4623 23.3585 18.7382 23.4845 19.0419C23.6105 19.3457 23.6754 19.6713 23.6754 20.0001C23.6754 20.3289 23.6105 20.6545 23.4845 20.9583C23.3585 21.262 23.1738 21.5379 22.9409 21.7701L18.8559 25.8551C18.8092 25.9014 18.7538 25.9381 18.6929 25.963C18.632 25.9879 18.5667 26.0005 18.5009 26.0001Z"
												fill="currentColor"
											/>
										</g>
										<rect x="0.5" y="0.5" width="39" height="39" rx="19.5" stroke="currentColor" />
										<defs>
											<clipPath id="clip0_4202_908">
												<rect
													width="12"
													height="12"
													fill="currentColor"
													transform="translate(15 14)"
												/>
											</clipPath>
										</defs>
									</svg>
								</div>
							</div>
							<div className="first-screen__thumbs">
								<Swiper
									onSwiper={setThumbsSwiper}
									modules={[Thumbs]}
									slidesPerView={"auto"}
									spaceBetween={16}
									speed={1000}
									watchSlidesProgress={true}
									className="main-slider-thumbs"
									slidesOffsetAfter={80}
									slidesOffsetBefore={80}
									breakpoints={{
										769: {
											spaceBetween: 32,
										},
										1440: {
											spaceBetween: 42,
											slidesPerView: 4,
											slidesOffsetAfter: 0,
										},
									}}
								>
									{slidesData.map(({ title, image, id }) => (
										<SwiperSlide key={id} className="main-slider-thumbs__slide">
											<div className="main-slider-thumbs__slide-image">
												<img
													src={supportsWebP ? image.thumbWebp : image.thumbSrc}
													alt={image.alt}
												/>
											</div>
											<div className="main-slider-thumbs__slide-text">{title}</div>
										</SwiperSlide>
									))}
								</Swiper>
							</div>
						</div>
					</Swiper>
				</>
			)}
		</>
	);
};

export default MainSlider;
