import React from "react";
import webPCheck from "supports-webp";

const useWebPSupportCheck = () => {
    const [supportsWebP, setSupportsWebP] = React.useState(false);

    React.useEffect(() => {
        checkSupport();
    }, []);

    const checkSupport = async () => {
        const result = await webPCheck.then(setSupportsWebP);
    }

    return supportsWebP;
}

export default useWebPSupportCheck;