import React from "react";

import { useMockAPI } from "../../hooks";
import BlockArticle from "../blockArticle/BlockArticle";
import BlockArticleSkeleton from "../blockArticleSkeleton/BlockArticleSkeleton";
import MoreButton from "../moreButton/MoreButton";

import "./sectionArticlesMobile.scss";

const SectionArticlesMobile = () => {
	const { loading, error, getItems } = useMockAPI();
	const [articles, setArticles] = React.useState(null);

	React.useEffect(() => {
		getArticles();
	}, []);

	const getArticles = async () => {
		const request = await getItems("politic-articles", 1, 3).then(setArticles);
	};
	return (
		<div className="section-articles-mobile">
			{loading && !articles && (
				<ul className="section-articles-mobile__list" aria-hidden>
					{new Array(3).fill(null).map((item, i) => (
						<BlockArticleSkeleton key={i} />
					))}
				</ul>
			)}
			{!error && articles && articles.length && (
				<ul className="section-articles-mobile__list">
					{articles.map(({ id, ...item }) => (
						<BlockArticle key={id} className="section-articles-mobile__item" {...item} />
					))}
				</ul>
			)}
			<MoreButton className="section-articles-mobile__more" label="" url="#" />
		</div>
	);
};

export default SectionArticlesMobile;
